<template>
    <div>
        <v-tooltip :color="color" bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-progress-linear
                    :color="color"
                    :value="averageResponse"
                    height="6"
                    v-bind="attrs"
                    v-on="on"
                ></v-progress-linear>
            </template>
            <span>{{ message }}</span>
        </v-tooltip>
        <div>{{ average }}</div>
    </div>
</template>

<script>
export default {
    name: 'YesNoResultStatus',
    props: {
        responseItem: {
            type: Object,
            required: true,
            default: () => {
                return {};
            },
        },
    },
    data() {
        return {
            responseSummary: null,
        };
    },
    computed: {
        color() {
            return this.averageResponse >= 50 ? 'success' : 'error';
        },
        message() {
            return this.averageResponse
                ? this.averageResponse + '% yes'
                : 'No Responses';
        },
        average() {
            return this.averageResponse
                ? this.averageResponse + '% yes'
                : 'No Responses';
        },
        averageResponse() {
            // console.log(this.responseItem);
            const yesPercentage =
                (this.responseItem.responses[1].responseCount /
                    this.responseItem.responseCount) *
                100;
            // console.log(yesPercentage);
            if (isNaN(yesPercentage)) {
                console.log('bad');
                return null;
            } else {
                return yesPercentage.toFixed(0);
            }
        },
    },
};
</script>
