<template>
    <v-container class="py-10" fluid>
        <v-row class="justify-center">
            <v-col cols="12" md="10">
                <v-alert
                    v-model="alert"
                    :type="alertType"
                    border="left"
                    close-text="Close Alert"
                    elevation="10"
                    dismissible
                    transition="scale-transition"
                    style="
                        position: fixed;
                        top: 20%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        z-index: 9999;
                        max-width: 35rem;
                    "
                >
                    {{ alertMessage }}
                </v-alert>
                <v-card class="rounded-lg pb-2">
                    <v-toolbar color="green" dark>
                        <v-toolbar-title>Survey Responses</v-toolbar-title>
                        <v-spacer />
                        <v-btn
                            color="green"
                            class="white--text ml-2"
                            fab
                            small
                            rounded
                            @click="openAddDialog"
                        >
                            <v-icon> mdi-plus </v-icon>
                        </v-btn>
                    </v-toolbar>
                    <v-dialog
                        v-model="addDialog"
                        :fullscreen="this.$vuetify.breakpoint.smAndDown"
                        max-width="800"
                    >
                        <v-card class="rounded-lg pb-2">
                            <v-toolbar color="green" dark>
                                <v-toolbar-title>
                                    Create Question
                                </v-toolbar-title>
                            </v-toolbar>
                            <v-container>
                                <v-row>
                                    <v-col>
                                        <p>Question Type:</p>
                                        <v-select
                                            v-model="addItem.questionType"
                                            :items="questionTypeOptions"
                                            :hint="selectHint"
                                            @input="showHint"
                                        ></v-select>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col>
                                        <p>Question Text:</p>
                                        <v-text-field
                                            v-model="addItem.questionText"
                                        />
                                    </v-col>
                                </v-row>

                                <v-row>
                                    <v-col>
                                        <p>Enabled:</p>
                                        <v-checkbox
                                            v-model="addItem.enabled"
                                            label="Enable Question"
                                        ></v-checkbox>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-btn
                                        class="ma-2"
                                        color="error"
                                        @click="addDialog = false"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        class="ma-2"
                                        color="success"
                                        @click="createQuestion"
                                        >Add</v-btn
                                    >
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-dialog>
                    <v-dialog
                        v-model="editDialog"
                        :fullscreen="this.$vuetify.breakpoint.smAndDown"
                        max-width="800"
                    >
                        <v-card class="rounded-lg pb-2">
                            <v-toolbar color="green" dark>
                                <v-toolbar-title>
                                    Edit Question
                                </v-toolbar-title>
                            </v-toolbar>
                            <v-container>
                                <v-row>
                                    <v-col>
                                        <p>Question Type:</p>
                                        <v-select
                                            v-model="editItem.questionType"
                                            :items="questionTypeOptions"
                                            :placeholder="editItem.questionType"
                                            :hint="selectHint"
                                            @input="showHint"
                                        ></v-select>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-col>
                                        <p>Question Text:</p>
                                        <v-text-field
                                            v-model="editItem.questionText"
                                        />
                                    </v-col>
                                </v-row>

                                <v-row>
                                    <v-col>
                                        <p>Enabled:</p>
                                        <v-checkbox
                                            v-model="editItem.enabled"
                                            label="Enable Question"
                                        ></v-checkbox>
                                    </v-col>
                                </v-row>
                                <v-row>
                                    <v-btn
                                        class="ma-2"
                                        color="error"
                                        @click="editDialog = false"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        class="ma-2"
                                        color="success"
                                        @click="updateQuestion"
                                        >Update</v-btn
                                    >
                                </v-row>
                            </v-container>
                        </v-card>
                    </v-dialog>
                    <template>
                        <v-data-table
                            :headers="computedHeaders"
                            :items="surveyData"
                            :items-per-page="5"
                            :loading="dataLoading"
                            loading-text="Loading... Please wait"
                            show-expand
                            single-expand
                        >
                            <template v-slot:[`item.responseRate`]="{ item }">
                                {{ item.responseRate | percentage }}
                            </template>
                            <template
                                v-slot:[`item.responseSummary`]="{ item }"
                            >
                                <td>
                                    <yes-no-result-status
                                        v-if="item.questionType === 'yes_no'"
                                        :responseItem="item"
                                    />
                                    <scaled-result-status
                                        v-else-if="
                                            item.questionType === 'scaled'
                                        "
                                        :responseItem="item"
                                    />
                                </td>
                            </template>
                            <template v-slot:[`item.actions`]="{ item }">
                                <v-menu
                                    transition="scroll-y-transition"
                                    bottom
                                    left
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn icon v-bind="attrs" v-on="on">
                                            <v-icon> mdi-dots-vertical </v-icon>
                                        </v-btn>
                                    </template>
                                    <v-list>
                                        <v-list-item>
                                            <v-btn
                                                color="warning"
                                                text
                                                @click="openEditDialog(item)"
                                            >
                                                <v-icon small left>
                                                    mdi-pencil
                                                </v-icon>
                                                Edit
                                            </v-btn>
                                        </v-list-item>

                                        <v-list-item>
                                            <v-btn
                                                color="error"
                                                text
                                                @click="deleteQuestion(item)"
                                            >
                                                <v-icon small left>
                                                    mdi-delete
                                                </v-icon>
                                                Delete
                                            </v-btn>
                                        </v-list-item>
                                    </v-list>
                                </v-menu>
                            </template>
                            <template v-slot:no-data>
                        <v-alert prominent text type="info" class="ma-2">
                            <v-row align="center">
                                <v-col class="grow"> No records found </v-col>
                                <v-col class="shrink">
                                    <v-btn
                                        color="green"
                                        class="my-2 white--text"
                                        @click="initialize"
                                    >
                                        Retry
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-alert>
                    </template>
                            <template v-slot:expanded-item="{ item }">
                                <td :colspan="100">
                                    <div class="mx-15 my-2">
                                        <v-data-table
                                            :items="item.responses"
                                            :headers="responseHeaders"
                                            hide-default-footer
                                        >
                                        </v-data-table>
                                    </div>
                                </td>
                            </template>
                        </v-data-table>
                    </template>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

import ScaledResultStatus from '../../components/survey/ScaledResultStatus.vue';
import YesNoResultStatus from '../../components/survey/YesNoResultStatus.vue';

export default {
    name: 'SurveyResultsList',
    components: { YesNoResultStatus, ScaledResultStatus },
    data() {
        return {
            results: false,
            scaled: false,
            surveyData: [],
            dataLoading: false,
            addDialog: false,
            editDialog: false,
            addItem: {
                questionText: '',
                questionType: '',
                enabled: false,
            },
            editItem: {
                id: '',
                questionText: '',
                questionType: '',
                enabled: false,
            },
            alert: false,
            alertMessage: '',
            alertType: 'success',
            submitButton: '',
            selectHint: '',

            questionType: '',

            questionTypeOptions: ['Scaled', 'Yes or no'],
            responseHeaders: [
                {
                    text: 'Response Options',
                    value: 'answerDescription',
                    sortable: false,
                },
                {
                    text: 'Amount',
                    value: 'responseCount',
                    sortable: false,
                },
            ],
        };
    },
    computed: {
        ...mapGetters(['getInternalCoreClearController']),
        computedHeaders() {
            return [
                {
                    text: 'Question',
                    align: 'start',
                    sortable: true,
                    value: 'questionText',
                },
                {
                    text: 'Number of Responses',
                    value: 'responseCount',
                },
                {
                    text: 'Response Rate',
                    value: 'responseRate',
                },
                {
                    text: 'Total Transaction Count',
                    value: 'transactionCount',
                },
                {
                    text: 'Response Summary',
                    value: 'responseSummary',
                },
                { text: 'Actions', value: 'actions', sortable: false },
            ];
        },
    },
    mounted() {
        this.initialize();
    },
    methods: {
        initialize() {
            this.getData()
        },
        // need to rework this method to make it efficient
        async getData() {
            this.dataLoading = true;

            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController}/survey_questions`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    console.log(response.data);
                    await Promise.all(
                        response.data.results.map(async (result) => {
                            // console.log(result);
                            try {
                                let dataResponse = await axios.get(
                                    `${this.getInternalCoreClearController}/survey_results`,
                                    {
                                        withCredentials: true,
                                        crossDomain: true,
                                        params: { question_id: result.id },
                                    },
                                );
                                if (dataResponse.data) {
                                    // console.log(dataResponse.data);
                                    this.surveyData.push(dataResponse.data);
                                }
                                // console.log(this.surveyData);
                            } catch (error) {
                                console.error('Could not get survey questions');
                                console.error(error);
                            }
                        }),
                    );
                } else {
                    console.log('could not get data');
                }

                this.dataLoading = false;
            } catch (error) {
                console.error('Could not get survey questions');
                console.error(error);
            }
        },
        async getQuestionDetail(item) {
            this.dataLoading = true;
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController}/survey_question`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: { question_id: item.id },
                    },
                );
                if (response.data) {
                    // console.log(response.data);
                    this.editItem = response.data;
                }
                this.dataLoading = false;
            } catch (error) {
                console.error('Could not get survey questions');
                console.error(error);
            }
        },
        openAddDialog() {
            this.addItem = {
                questionText: '',
                questionType: '',
                enabled: false,
            }
            this.addDialog = true;
        },
        async openEditDialog(item) {
            // console.log(this.surveyData);
            // console.log(item);
            await this.getQuestionDetail(item);
            // console.log(this.editItem);
            if (item.questionType == 'scaled') {
                this.editItem.questionType = 'Scaled';
            } else if (item.questionType == 'yes_no') {
                this.editItem.questionType = 'Yes or no';
            }
            this.editDialog = true;
        },

        async createQuestion() {
            // console.log('create method');
            // console.log(this.addItem);

            let formData = new FormData();
            formData.append(
                'question_type',
                this.formatQuestionType(this.addItem.questionType),
            );
            formData.append('question_text', this.addItem.questionText);
            formData.append(
                'enabled',
                this.formatEnabledValue(this.addItem.enabled),
            );
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/update_survey_question`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    // console.log('question created');
                    // console.log(response.data);
                    this.alert = true;
                    this.alertMessage = 'Question Created';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('problem occured');
                    this.alert = true;
                    this.alertMessage = 'A Problem Occured';
                    this.alertType = 'error';
                }
            } catch (error) {
                console.error('Could not update survey questions');
                console.error(error);
            }
        },

        async updateQuestion() {
            // console.log(this.editItem);
            // console.log(this.editItem.id);
            let formData = new FormData();
            formData.append('question_id', this.editItem.id);
            formData.append(
                'question_type',
                this.formatQuestionType(this.editItem.questionType),
            );
            formData.append('question_text', this.editItem.questionText);
            formData.append(
                'enabled',
                this.formatEnabledValue(this.editItem.enabled),
            );
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/update_survey_question`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    // console.log('question updated?');
                    // console.log(response.data);
                    this.alert = true;
                    this.alertMessage = 'Question Updated';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('problem occured');
                    this.alert = true;
                    this.alertMessage = 'A Problem Occured';
                    this.alertType = 'error';
                }
            } catch (error) {
                console.error('Could not update survey questions');
                console.error(error);
            }
        },
        async deleteQuestion(item) {
            // console.log(item.id);
            let formData = new FormData();
            formData.append('question_id', item.id);
            try {
                let response = await axios.post(
                    `${this.getInternalCoreClearController}/delete_survey_question`,
                    formData,

                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );
                if (response.data.success === true) {
                    // console.log('question deleted?');
                    // console.log(response);
                    this.alert = true;
                    this.alertMessage = 'Question Deleted';
                    this.alertType = 'success';
                    setTimeout(() => {
                        location.reload();
                    }, 1500);
                } else {
                    console.log('problem occured');
                    this.alert = true;
                    this.alertMessage = 'A Problem Occured';
                    this.alertType = 'error';
                }
            } catch (error) {
                console.error('Could not delete survey questions');
                console.error(error);
            }
        },
        showHint(item) {
            if (item == 'Yes or no') {
                this.selectHint = 'Requires a binary yes or no response';
            } else if (item == 'Scaled') {
                this.selectHint = 'Provides a 5 point scale as a rating method';
            }
        },
        formatEnabledValue(item) {
            // console.log(item);
            let newVal = 0;
            if (item === false) {
                newVal = 0;
            } else if (item === true) {
                newVal = 1;
            } else {
                console.log('problem');
            }
            // console.log(newVal);
            return newVal;
        },
        formatQuestionType(item) {
            if (item == 'Yes or no') {
                return 'yes_no';
            } else if (item == 'Scaled') {
                return 'scaled';
            }
        },
    },
    filters: {
        percentage: (value) => {
            if (!value) return '0%';

            return (value * 100).toFixed(2) + '%';
        },
    },
};
</script>
