<template>
    <div>
        <v-tooltip :color="color" bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-progress-linear
                    :color="color"
                    :value="formattedAverageResponse"
                    height="6"
                    v-bind="attrs"
                    v-on="on"
                ></v-progress-linear>
            </template>
            <span>{{ message }}</span>
        </v-tooltip>
        <div>{{ averageResponse }}</div>
    </div>
</template>

<script>
export default {
    name: 'ScaledResultStatus',
    props: {
        responseItem: {
            type: Object,
            required: true,
            default: () => {
                return {};
            },
        },
    },
    data() {
        return {
            responseSummary: null,
            // testCount: 0,
            // minValue: 1,
            // maxValue: 10,
        };
    },
    mounted() {
        // console.log('Scaled Response');
        // console.log(this.responseItem);
    },
    computed: {
        color() {
            return this.averageResponse >= 3.5 ? 'success' : 'error';
            // return this.testCount > 5 ? 'success' : 'error';
        },
        message() {
            return this.averageResponse ? this.averageResponse : 'No Responses';
        },
        averageResponse() {
            // console.log(this.responseItem);

            let total = 0;

            this.responseItem.responses.forEach((response) => {
                total += response.answerKey * response.responseCount;
            });

            const rawAvg = total / this.responseItem.responseCount;
            const avg = rawAvg.toFixed(2);
            console.log(avg);
            if (isNaN(avg)) {
                return 'No Responses';
            } else {
                return avg;
            }
        },
        formattedAverageResponse() {
            return this.averageResponse * 20;
        },
    },
};
</script>
